import Slider from '@mui/material/Slider';
import Stack from '@mui/system/Stack';
import React, { useEffect } from 'react';
import { useBeadApi } from './BeadApi';
import { FloatSlider } from './FloatSlider';
import { ColorSettings } from './model';



export function ColorSettingsEdit() {

    const api = useBeadApi();
    const [settings, setSettings] = React.useState<ColorSettings>({
        brightness: 0.0,
        contrast: 0.0
    });

    useEffect(() => {
        api.set_settings(settings);
    }, [settings]);


    return (
        <Stack>
            <p>Brightness</p>
            <FloatSlider
                value={settings.brightness}
                min={-2.0}
                max={2.0}
                onChange={(value) => setSettings(
                    {
                        ...settings,
                        brightness: value,
                    }
                )}
            />
            <p>Contrast</p>
            <FloatSlider
                value={settings.contrast}
                min={-2.0}
                max={2.0}
                onChange={(value) => setSettings(
                    {
                        ...settings,
                        contrast: value,
                    }
                )}
            />
        </Stack>
    )
}