import Slider from "@mui/material/Slider";

export function FloatSlider(props: { value: number, min: number, max: number, onChange?: (value: number) => void }) {

    const handleChange = (tick: number | number[]) => {
        if (props.onChange) {
            props.onChange(tick as number);
        }
    };
    const step = (props.max - props.min) / 100.0;

    return <Slider
        getAriaLabel={() => 'X'}
        value={props.value}
        min={props.min}
        max={props.max}
        step={step}
        onChange={(ev, numbers) => handleChange(numbers)}
        valueLabelDisplay="auto"
    />
}
