import React, { useEffect } from 'react';
import * as bead from "bead";
import ProfileEditor from './ProfileEdit';

// maybe this should be done in rust?
function drawPattern(pattern: bead.Pattern, context: CanvasRenderingContext2D) {
    const width = context.canvas.width;
    const height = context.canvas.height;

    const patternSize = Math.max(pattern.width(), pattern.height());
    let beadSize = Math.min(context.canvas.width, context.canvas.height) / patternSize;
    // if (beadSize > 10) {
    //     beadSize = 10;
    // }

    context.clearRect(0.0, 0.0, width, height);
    const cx = width / 2.0;
    const cy = height / 2.0;
    const px = pattern.width() / 2;
    const py = pattern.height() / 2;

    for (let y = 0; y < pattern.height(); y++) {
        for (let x = 0; x < pattern.width(); x++) {
            const rx = (x - px) * beadSize + cx;
            const ry = (y - py) * beadSize + cy;
            context.fillStyle = pattern.color_at(x, y);
            context.fillRect(rx, ry, beadSize, beadSize);
            context.strokeRect(rx, ry, beadSize, beadSize);
        }

    }
}



function PatternDisplay(props: { pattern?: bead.Pattern }) {
    const canvasRef = React.useRef<HTMLCanvasElement>(null);

    useEffect(() => {
        if (!props.pattern) return;
        if (!canvasRef.current) return;
        const context2d = canvasRef.current.getContext?.("2d");
        if (!context2d) return;

        drawPattern(props.pattern, context2d);
    }, [canvasRef, props.pattern]);

    if (!props.pattern) {
        return null;
    }

    return <div className='PatternDisplay'>
        <div style={{ textAlign: 'center' }}>Size: {props.pattern.width()} x {props.pattern.height()}</div>
        <canvas ref={canvasRef} width="800" height="800" ></canvas>
    </div >

}

export default PatternDisplay;